import { EntityTypeCode } from '../enums/EntityTypeCode';

/*  
*/
export class HealthEventFindRequest {
 
    // Filter by folder (empty for no filter) 
    public folderId: string;
 
    // Filter events after the provided timestamp (epoch milliseconds) 
    public from: number;
 
    // Filter events before the provided timestamp (epoch milliseconds) 
    public to: number;
 
    // Filter by health event source type 
    public source: EntityTypeCode;
 
    // Filter by source entity id(s) 
    public entityId: string[];
 
    // Sort by field and direction: (e.g. time+ = sort by time asc, id- = sort by id desc) 
    public sort: string;
 
    // Page number (for pagination) 
    public page: number;
 
    // Number of items per page (for pagination) 
    public pageSize: number;
 
    constructor(folderId?: string, from?: number, to?: number, source?: EntityTypeCode, entityId?: string[], sort?: string, page?: number, pageSize?: number) { 
        this.folderId = folderId;
        this.from = from;
        this.to = to;
        this.source = source;
        this.entityId = entityId;
        this.sort = sort;
        this.page = page;
        this.pageSize = pageSize;
    }

}


