
/*  
*/
export class RulesServiceArmDisarmRequest {
 
    // Arm/Disarm all sensors under the folder (and all sub folders) 
    public folderId: string;
 
    // Arm/Disarm all sensors under the folder (and all sub folders) identified by external Id 
    public folderExtId: string;
 
    // Arm/Disarm all provided sensor by Ids 
    public sensorId: string[];
 
    // Arm/Disarm all provided sensor by their external Ids 
    public sensorExtId: string[];
 
    constructor(folderId?: string, folderExtId?: string, sensorId?: string[], sensorExtId?: string[]) { 
        this.folderId = folderId;
        this.folderExtId = folderExtId;
        this.sensorId = sensorId;
        this.sensorExtId = sensorExtId;
    }

}


