
/*  
*/
export class TrafficAnalysisReportRequest {
 
    // Filter by sensor(s) 
    public sensorId: string[];
 
    // Filter by folder(s) 
    public folderId: string[];
 
    // Filter report after the provided timestamp (epoch milliseconds) 
    public from: number;
 
    // Filter report before the provided timestamp (epoch milliseconds) 
    public to: number;
 
    constructor(sensorId?: string[], folderId?: string[], from?: number, to?: number) { 
        this.sensorId = sensorId;
        this.folderId = folderId;
        this.from = from;
        this.to = to;
    }

}


