import { EntityTypeCode } from '../enums/EntityTypeCode';
import { IntegrationStatusCode } from '../enums/IntegrationStatusCode';
import { BaseEntity } from '../entities/BaseEntity';

/* 
   Health event type (combines multiple health events: sensor health event, appliance health event...) 
*/
export class HealthEvent extends BaseEntity {
 
    // Account Id 
    public accountId: string;
 
    // Source of health event (can be only APPLIANCE or AGENT or SENSOR) 
    public source: EntityTypeCode;
 
    // Source Entity Id 
    public entityId: string;
 
    // Source Entity external Id 
    public entityExtId: string;
 
    // Source Entity Name 
    public entityName: string;
 
    // When the health status has changed [Epoch milliseconds Timestamp] 
    public startedOn: number;
 
    // When the health event was closed [Epoch milliseconds Timestamp] 
    public closedOn: number;
 
    // Entity status 
    public status: number;
 
    // Entity real time state bit mask flags 
    public state: number;
 
    // Event integration status 
    public integrationStatus: IntegrationStatusCode;
 
    // Entity Id 
    public id: string;
 
    // Entity document type (system field) 
    public _type: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    public createdOn: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    public updatedOn: number;
 

}


