import { BehaviorTypeCode } from '../enums/BehaviorTypeCode';
import { ObjectTypeReport } from '../common/ObjectTypeReport';

/* 
   Statistic report record (generated by statistic rule periodically) 
*/
export class StatisticReport {
 
    // Account Id 
    public accountId: string;
 
    // Sensor Id 
    public sensorId: string;
 
    // Rule Id 
    public ruleId: string;
 
    // Detected behavior type (UNKNOWN | CROSSING | MOVING | GROUPING | OCCUPANCY ...) 
    public behaviorType: BehaviorTypeCode;
 
    // The start time of this report interval [Epoch milliseconds Timestamp] 
    public reportIntervalStart: number;
 
    // The end time of this report interval [Epoch milliseconds Timestamp] 
    public reportIntervalEnd: number;
 
    // Total Count for all classes 
    public totalCount: number;
 
    // The average time between vehicle crossing (milliseconds) 
    public avgTimeBetween: number;
 
    // Average speed -  (meters/second) 
    public avgSpeed: number;
 
    // Detailed report by object type 
    public reportDetails: ObjectTypeReport[];
 
    constructor(accountId?: string, sensorId?: string, ruleId?: string, behaviorType?: BehaviorTypeCode, reportIntervalStart?: number, reportIntervalEnd?: number, totalCount?: number, avgTimeBetween?: number, avgSpeed?: number, reportDetails?: ObjectTypeReport[]) { 
        this.accountId = accountId;
        this.sensorId = sensorId;
        this.ruleId = ruleId;
        this.behaviorType = behaviorType;
        this.reportIntervalStart = reportIntervalStart;
        this.reportIntervalEnd = reportIntervalEnd;
        this.totalCount = totalCount;
        this.avgTimeBetween = avgTimeBetween;
        this.avgSpeed = avgSpeed;
        this.reportDetails = reportDetails;
    }

}


