import { EdgeEventInfoBody } from '../common/EdgeEventInfoBody';

/* 
   Edge Raw event info message 
*/
export class EdgeEventInfoMessage {
 
    // Message Id 
    public id: number;
 
    // Message Op Code (50) 
    public op: number;
 
    // Protocol version (1) 
    public ver: number;
 
    // Message body 
    public body: EdgeEventInfoBody;
 
    constructor(id?: number, op?: number, ver?: number, body?: EdgeEventInfoBody) { 
        this.id = id;
        this.op = op;
        this.ver = ver;
        this.body = body;
    }

}


