import { DigitalIOStatusCode } from '../enums/DigitalIOStatusCode';
import { PortMapping } from '../entities/PortMapping';
import { BaseEntity } from '../entities/BaseEntity';

/* 
   Digital IO type 
*/
export class DigitalIO extends BaseEntity {
 
    // Account Id 
    public accountId: string;
 
    // Appliance Id 
    public applianceId: string;
 
    // Device name 
    public name: string;
 
    // Device type (e.g. Axis P8221) 
    public deviceType: string;
 
    // Device connection URI (RFC 6570 format) 
    public deviceUri: string;
 
    // Device status 
    public status: DigitalIOStatusCode;
 
    // Device IO port mapping 
    public portMapping: PortMapping[];
 
    // Entity Id 
    public id: string;
 
    // Entity document type (system field) 
    public _type: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    public createdOn: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    public updatedOn: number;
 

}


