
/* 
   Flatten appliance log entry to store as a single document in the elastic search 
*/
export class ApplianceLogEntry {
 
    // Unique Id (applianceId + containerId + timestamp) 
    public id: string;
 
    // Account Id 
    public accountId: string;
 
    // Appliance Id 
    public applianceId: string;
 
    // Container Id 
    public containerId: string;
 
    // Container Name 
    public containerName: string;
 
    // Container Tag 
    public containerTag: string;
 
    // Configuration version Id 
    public versionId: string;
 
    // Severity (Log Level) 
    public severity: string;
 
    // Entry time [ISO8601 format] 
    public timestamp: string;
 
    // Log entry raw text 
    public textPayload: string;
 
    constructor(id?: string, accountId?: string, applianceId?: string, containerId?: string, containerName?: string, containerTag?: string, versionId?: string, severity?: string, timestamp?: string, textPayload?: string) { 
        this.id = id;
        this.accountId = accountId;
        this.applianceId = applianceId;
        this.containerId = containerId;
        this.containerName = containerName;
        this.containerTag = containerTag;
        this.versionId = versionId;
        this.severity = severity;
        this.timestamp = timestamp;
        this.textPayload = textPayload;
    }

}


