import { EntityTypeCode } from '../enums/EntityTypeCode';
import { SensorStatusCode } from '../enums/SensorStatusCode';
import { TimeFrame } from '../common/TimeFrame';

/* 
   Tree item for account hierarchy tree 
*/
export class TreeItem {
 
    // Item Id 
    public itemId: string;
 
    // Tree item entity type 
    public type: EntityTypeCode;
 
    // Tree item name 
    public name: string;
 
    // Item type name (ACCOUNT | FOLDER | APPLIANCE | SENSOR) 
    public typeName: string;
 
    // Sensor status: UNDEFINED | ACTIVE | WARNING | ERROR | INACTIVE | SUSPENDED | PENDING 
    public status: SensorStatusCode;
 
    // Inactive analytics for all sensors for the specified time frame 
    public shunt: TimeFrame;
 
    constructor(itemId?: string, type?: EntityTypeCode, name?: string, typeName?: string, status?: SensorStatusCode, shunt?: TimeFrame) { 
        this.itemId = itemId;
        this.type = type;
        this.name = name;
        this.typeName = typeName;
        this.status = status;
        this.shunt = shunt;
    }

}


