
/*  
*/
export class RulesServicePauseRequest {
 
    // Pause (stop) analytics detection for all sensors under the folder (and all sub folders) 
    public folderId: string;
 
    // Pause (stop) analytics detection for all sensors under the folder (and all sub folders) identified by external Id 
    public folderExtId: string;
 
    // From timestamp (epoch milliseconds) 
    public from: number;
 
    // To timestamp (epoch milliseconds) 
    public to: number;
 
    constructor(folderId?: string, folderExtId?: string, from?: number, to?: number) { 
        this.folderId = folderId;
        this.folderExtId = folderExtId;
        this.from = from;
        this.to = to;
    }

}


