import { EntityTypeCode } from '../enums/EntityTypeCode';

/* 
   Mapping description of digital IO port 
*/
export class PortMapping {
 
    // Port name or number 
    public port: string;
 
    // Output (true) or input (false) port 
    public isOutput: boolean;
 
    // Type of associated entity (RULE | SENSOR | FOLDER) 
    public entityType: EntityTypeCode;
 
    // Rule/Sensor/Folder Id to associate with the port (according to entityType) 
    public entityId: string;
 
    // Activate/Deactivate rules on input port trigger / Activate/Deactivate output port on rule trigger (event) 
    public activate: boolean;
 
    // Enable or disable the action of this port mapping 
    public enabled: boolean;
 
    constructor(port?: string, isOutput?: boolean, entityType?: EntityTypeCode, entityId?: string, activate?: boolean, enabled?: boolean) { 
        this.port = port;
        this.isOutput = isOutput;
        this.entityType = entityType;
        this.entityId = entityId;
        this.activate = activate;
        this.enabled = enabled;
    }

}


