import { SensorStatus } from '../entities/SensorStatus';
import { AgentStatus } from '../entities/AgentStatus';
import { ApplianceStatus } from '../entities/ApplianceStatus';

/* 
   @WebSocketMessage
   This message is pushed to the client upon any new event in the registered account 
*/
export class SocketHealthStatusNotification {
 
    // Message OpCode (1041) 
    public op: number;
 
    // Error message (if error occurred) 
    public error: string;
 
    // Sensor status 
    public sensorStatus: SensorStatus;
 
    // Agent status 
    public agentStatus: AgentStatus;
 
    // Appliance status 
    public applianceStatus: ApplianceStatus;
 
    constructor(op?: number, error?: string, sensorStatus?: SensorStatus, agentStatus?: AgentStatus, applianceStatus?: ApplianceStatus) { 
        this.op = op;
        this.error = error;
        this.sensorStatus = sensorStatus;
        this.agentStatus = agentStatus;
        this.applianceStatus = applianceStatus;
    }

}


