import { EdgeMetadataBody } from '../common/EdgeMetadataBody';

/* 
   Edge Metadata message 
*/
export class EdgeMetadataMessage {
 
    // Message Id 
    public id: number;
 
    // Message Op Code (3) 
    public op: number;
 
    // Protocol version (1) 
    public ver: number;
 
    // Message body 
    public body: EdgeMetadataBody;
 
    constructor(id?: number, op?: number, ver?: number, body?: EdgeMetadataBody) { 
        this.id = id;
        this.op = op;
        this.ver = ver;
        this.body = body;
    }

}


