
/*  
*/
export class SysSensorsFetchObjectsCropsRequest {
 
    // Account Id 
    public accountId: string;
 
    // Sensor Id 
    public sensorId: string;
 
    // List of timestamps (epoch time milliseconds) 
    public ts: number[];
 
    // List of object Ids 
    public objectId: number[];
 
    // Name of the folder only (in the pre-configured bucket) to store the cropped images 
    public bucketFolder: string;
 
    constructor(accountId?: string, sensorId?: string, ts?: number[], objectId?: number[], bucketFolder?: string) { 
        this.accountId = accountId;
        this.sensorId = sensorId;
        this.ts = ts;
        this.objectId = objectId;
        this.bucketFolder = bucketFolder;
    }

}


