
/*  
*/
export class AppliancesServiceGetLogsRequest {
 
    // Appliance Id 
    public id: string;
 
    // Logs request range start timestamp (Epoch millis) 
    public from: number;
 
    // Logs request range end timestamp (Epoch millis) - 0 means until now 
    public to: number;
 
    // List of components to get logs (empty list means get ALL containers logs) 
    public component: string[];
 
    constructor(id?: string, from?: number, to?: number, component?: string[]) { 
        this.id = id;
        this.from = from;
        this.to = to;
        this.component = component;
    }

}


