
/* 
   Edge Event Image 
*/
export class EdgeEventImage {
 
    // Event image jpeg (base64 encoded string) 
    public jpg: string;
 
    // Object instance timestamp [Epoch milliseconds Timestamp] 
    public ts: number;
 
    constructor(jpg?: string, ts?: number) { 
        this.jpg = jpg;
        this.ts = ts;
    }

}


