import { ObjectTypeCode } from '../enums/ObjectTypeCode';
import { LineCrossDirectionCode } from '../enums/LineCrossDirectionCode';

/* 
   Specific report by object typeis generated by statistic rule 
*/
export class ObjectTypeReport {
 
    // Detected object type (UNKNOWN | PERSON | VEHICLE | BUS | TRACK ...) 
    public objectType: ObjectTypeCode;
 
    // Line crossing direction (used only in case of line-crossing rule type) 
    public crossingDir: LineCrossDirectionCode;
 
    // Count of objects crossed 
    public count: number;
 
    // Average speed -  (meters/second) 
    public avgSpeed: number;
 
    constructor(objectType?: ObjectTypeCode, crossingDir?: LineCrossDirectionCode, count?: number, avgSpeed?: number) { 
        this.objectType = objectType;
        this.crossingDir = crossingDir;
        this.count = count;
        this.avgSpeed = avgSpeed;
    }

}


