
/* 
   Appliance log entries batch sent by the device manager 
*/
export class ApplianceLogBatch {
 
    // Container Id 
    public containerId: string;
 
    // Container Name 
    public containerName: string;
 
    // Container Tag 
    public containerTag: string;
 
    // Configuration version Id 
    public versionId: string;
 
    // Raw log entries 
    public logs: string[];
 
    constructor(containerId?: string, containerName?: string, containerTag?: string, versionId?: string, logs?: string[]) { 
        this.containerId = containerId;
        this.containerName = containerName;
        this.containerTag = containerTag;
        this.versionId = versionId;
        this.logs = logs;
    }

}


