import { Thresholds } from '../common/Thresholds';

/* 
   Health thresholds specifications for sensor and appliances 
*/
export class HealthThresholds {
 
    // Active thresholds (false means inherit form parent level) 
    public isActive: boolean;
 
    // Health thresholds for sensors 
    public sensorsThresholds: Thresholds;
 
    // Health thresholds for appliances 
    public applianceThresholds: Thresholds;
 
    constructor(isActive?: boolean, sensorsThresholds?: Thresholds, applianceThresholds?: Thresholds) { 
        this.isActive = isActive;
        this.sensorsThresholds = sensorsThresholds;
        this.applianceThresholds = applianceThresholds;
    }

}


