import { BehaviorTypeCode } from '../enums/BehaviorTypeCode';
import { BoundingBox } from '../common/BoundingBox';
import { EdgeEventObject } from '../common/EdgeEventObject';
import { EdgeEventImage } from '../common/EdgeEventImage';
import { ObjectTypeCode } from '../enums/ObjectTypeCode';
import { SeverityTypeCode } from '../enums/SeverityTypeCode';

/* 
   Edge Event Info 
*/
export class EdgeEventInfo {
 
    // Detected behavior type 
    public behaviorType: BehaviorTypeCode;
 
    // Clip period time (N/A) 
    public clipPeriod: number;
 
    // Event description (N/A) 
    public description: string;
 
    // Array of detected object's bounding boxes 
    public eventBboxes: BoundingBox[];
 
    // Event unique Id 
    public eventId: string;
 
    // Event image time [Epoch milliseconds Timestamp] 
    public eventImageTime: number;
 
    // Detected object information 
    public eventObjects: EdgeEventObject[];
 
    // When the event was detected [Epoch milliseconds Timestamp] 
    public eventTime: number;
 
    // Event type (N/A) 
    public eventType: number;
 
    // External sensor Id 
    public externalId: string;
 
    // Event image array (include a single item) 
    public jpegBuffers: EdgeEventImage[];
 
    // Object type (class) 
    public objectType: ObjectTypeCode;
 
    // Rule Id 
    public ruleId: string;
 
    // Sensor Id 
    public sensorId: string;
 
    // Event severity level 
    public severity: SeverityTypeCode;
 
    constructor(behaviorType?: BehaviorTypeCode, clipPeriod?: number, description?: string, eventBboxes?: BoundingBox[], eventId?: string, eventImageTime?: number, eventObjects?: EdgeEventObject[], eventTime?: number, eventType?: number, externalId?: string, jpegBuffers?: EdgeEventImage[], objectType?: ObjectTypeCode, ruleId?: string, sensorId?: string, severity?: SeverityTypeCode) { 
        this.behaviorType = behaviorType;
        this.clipPeriod = clipPeriod;
        this.description = description;
        this.eventBboxes = eventBboxes;
        this.eventId = eventId;
        this.eventImageTime = eventImageTime;
        this.eventObjects = eventObjects;
        this.eventTime = eventTime;
        this.eventType = eventType;
        this.externalId = externalId;
        this.jpegBuffers = jpegBuffers;
        this.objectType = objectType;
        this.ruleId = ruleId;
        this.sensorId = sensorId;
        this.severity = severity;
    }

}


